//@ts-check


/*

Pas d'toile 


Tant que le rpo ne dploi pas, le status vaut `null`
Quand un build est annul par un nouveau commit, 

l'API de statut retourne d'abord une erreur et plus tard, `building` puis `built`

Quand on viens de commit et qu'on interroge l'API `/pages` 
elle nous rponds d'abord que c'est built (parce que le nouveau build n'a pas commenc).

https://docs.github.com/en/rest/pages?apiVersion=2022-11-28#get-a-github-pages-site

Peut-tre que l'on pourrait changer de stratgie et utiliser
`await octokit.request('GET /repos/{owner}/{repo}/deployments'` avec un 1 per_page

Puis, rcuprer

"statuses_url": "https://api.github.com/repos/octocat/example/deployments/1/statuses", 
pour ensuite faire du polling dessus, et rcuprer la proprit state 

    "state": {
        "description": "The state of the status.",
        "enum": [
          "error",
          "failure",
          "inactive",
          "pending",
          "success",
          "queued",
          "in_progress"
        ]
    }



NOTE
   
Nous pensons que Github laisse mourrir l'API github pages.

Un refactoring  faire plus tard serait d'utilise les API de dploiement qui sont plus riche en information.

Nous pourirons essayer de suivre un commit pour s'assurer que nous allons  la fin du dploiement.

Voir `databaseAPI.getLastDeployment(login, repoName)` et `databaseAPI.getDeploymentStatus(deployment)`


*/
export default function (databaseAPI, login, repoName) {
    /** @type {"building" | "built" | "errored"} */
    let repoStatus = "building";
    let reaction = undefined;
    let lastSuccessCheck;
    let lastErrorCheck;
    let timeout

    function scheduleCheck(delay = 5000) {
        if (!timeout) {
            timeout = setTimeout(() => {
                buildStatusObject.checkStatus()
                timeout = undefined;
            }, delay)
        }
    }

    const buildStatusObject = {
        get status() {
            return repoStatus;
        },
        subscribe(callback) {
            console.log("subscribe reaction.. ", callback)
            reaction = callback;
        },
        checkStatus() {
            return Promise.resolve(login).then(login => {
                return databaseAPI.getGitHubPagesSite(login, repoName)
                    .then(({ status }) => {
                        console.log('build status', status)

                        if (["built", "errored", "building"].includes(status)) {
                            repoStatus = status
                        } else {
                            // status === null
                            repoStatus = "building"
                        }

                        if (reaction) {
                            reaction(repoStatus);
                        }

                        if (repoStatus === "building") {
                            scheduleCheck()
                        }
                    })
                    .catch(error => {
                        repoStatus = 'errored'
                        if (reaction) {
                            reaction(repoStatus);
                        }
                    })
            })
        },
        setBuildingAndCheckStatusLater() {
            repoStatus = "building"
            clearTimeout(timeout)
            timeout = undefined
            scheduleCheck(30000)
        }
    }

    buildStatusObject.checkStatus();
    return buildStatusObject
}